import myButton from '../../../components/my-components/RoundButton';
import {
    number_format
} from '../../../components/my-components/formater.js'
export default {
    components: {
        myButton
    },
    props: {
         Jurisdiction:{
            type:Object,
            default:{},
            required:true
        },
        info: {
            type: Object,
            default: () => ({
                name: '',
                provide: '',
                num: 0,
                pay: 0,
                addtion: 0,
                payType: 'wx',
                subStatus: 0, //订单状态，1：维权
                status: 0, ////0待付款,1代发货，2待收货，3已完成，4已关闭
            })
        }
    },
    computed: {
        canSendGood() { //判断可否发货
            if (this.info.groups_success==1||this.info.groups_success===undefined) {
                return !!this.info.send_able ? "primary" : "disable"
            } else{
                return "disable";
            } 
        },
        showPayType() {
            return (this.info.status != 0 && this.info.status != 4) && this.info.payType == "wx"
        }
    },
    methods: {
        formatePrice(val) {
            return number_format(val, 2, '.', ',')
        },
        clickButton(val) {
            this.$emit('click', {
                type: 'button',
                detail: {
                    val
                }
            })
        },
        clickBill() {
            this.$emit('click', {
                type: 'info',
                detail: {
                    val: this.info
                }
            })
        }
    },
}
